










































import Vue from 'vue';
export default Vue.extend({
    name: 'Terminvorschau',
    data() {
        return {
            HTMLtext: '',
        };
    },
    computed: {
        validHTMLCode() {
            const pattern = new RegExp(
                '^((<("[^"]*"|\'[^\']*\'|[^\'">])*>)((.|\\n)*)(</("[^"]*"|\'[^\']*\'|[^\'">])*>)\\n*)*$'
            );
            return pattern.test((this as any).HTMLtext);
        },
    },
});
